.gh-section-dr {
    background-image: linear-gradient(222deg, #059E9E 0%, #330867 100%);
    overflow: hidden;
}
.gh-section-head {
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    -ms-flex-direction: column;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    flex-direction: column;
    -ms-flex-align: center;
    -webkit-box-align: center;
    align-items: center;
    margin-bottom: 1em;
    text-align: center;
}

.gh-section-white {
    border-top: 1px solid #e5eff5;
    border-bottom: 1px solid #e5eff5;
    background: #fff;
}

.gh-features-icon {
    display: -ms-flexbox;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 80px;
    background: radial-gradient(circle farthest-corner at right bottom, #d3e4ee 0, #fff 100%);
    border-radius: 100%;
    box-shadow: inset 0 0 0 1px hsla(0, 0%, 100%, .12), 0 0 1px rgba(0, 0, 0, .35), 0 2px 4px rgba(0, 0, 0, .2);   }
.bubble {
    position: absolute;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 100%;
    mix-blend-mode: soft-light;
}
.bubble-1 {
    height: 1000px;
    width: 1000px;
    top: -60%;
    left: -20%;
}
.bubble-2 {
    height: 555px;
    width: 555px;
    top: 20%;
    right: calc(-555px * 0.6);
}
.bubble-3 {
    height: 1350px;
    width: 1350px;
    bottom: calc(-1350px * 0.8);
    left: calc(50% - 675px);
    background: rgba(255, 255, 255, 0.06);
}
.magichr {
    height: 1px;
    width: 220px;
    margin: 0 auto 1.5em;
    background: linear-gradient(90deg, #05FBFF 0%, #330867 100%);
}

.shadow {
     max-height: 400px;
    flex-shrink: 0;
    border-radius: 5px;
    box-shadow: 8px 18px 50px rgba(39, 44, 49, .07), 1px 6px 12px rgba(39, 44, 49, .04);
}

.circle-right {
    right: calc(-700px / 2);
}
.circle {
    width: 700px;
    height: 700px;
    padding: 2px;
    border-radius: 100%;
    position: absolute;
    top: -100px;
    z-index: -1;
    background: linear-gradient(to bottom, #05FBFF, #330867);
    opacity: 0.2;
}
.site-header-content {
    position: relative;
    z-index: 100;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    min-height: 200px;
    max-height: 450px;
    padding: 10vw 4vw;
    text-align: center;
}
.editor-section {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.editor-section div:first-child {
    margin-right: 40px;
}
.editor-section-content {
    flex: 0 0 450px;
}
.copy {
    max-width: 760px;
    margin: 0 auto;
}





 .gh-foot {
     padding: 3vw 4vw 5vw;
     font-size: 1.3rem;
     user-select: none;
     background: #fff;
 }

 .gh-foot a {
     color: var(--midgrey);
     text-decoration: none;
 }

 .gh-foot a:hover {
     color: var(--darkgrey);
     text-decoration: none;
 }

 .gh-foot-content {
     display: flex;
     justify-content: space-between;
     padding-top: 20px;
     border-top: var(--whitegrey) 1px solid;
 }

 .gh-foot-nav {
     display: flex;
     justify-content: space-between;
     padding-left: 60px;
     width: 100%;
 }

 .gh-foot-col {
     padding: 0 10px;
 }

 .gh-foot-col h4 {
     margin: 5px 0 10px 0;
     font-size: 1.4rem;
 }

 .gh-foot-col ul {
     margin: 0;
     padding: 0;
     list-style: none;
 }

 .gh-foot-col ul a {
     display: block;
     padding: 3px 0;
     min-width: 90px;
 }

 .gh-foot-col li {
     margin: 0;
 }
 .gh-foot-col ul a {
     display: block;
     padding: 3px 0;
     min-width: 90px;
 }

 .gh-foot a {
     color: #738a94;
     text-decoration: none;
 }

 .gh-foot-col .gh-badge {
     position: relative;
     top: -1px;
 }
 .gh-badge {
     display: inline-block;
     padding: 2px 3px 3px;
     border: 1px solid #95bc37;
     color: #fff;
     font-size: .8em;
     line-height: 1em;
     font-weight: 600;
     text-align: center;
     text-shadow: 0 -1px 0 rgba(0, 0, 0, .1);
     white-space: nowrap;
     user-select: none;
     background: linear-gradient(#a4d037, #95bc37);
     border-radius: 2px;
     box-shadow: 0 1px 3px rgba(0, 0, 0, .15);
 }

 .gh-foot-mast {
     position: relative;
     flex: 0 0 300px;
     display: flex;
     flex-direction: column;
     align-items: flex-start;
 }

 .gh-foot-mast svg {
     display: inline-block;
     width: auto;
     height: 25px;
 }

 .gh-foot-mast-text {
     margin: 10px 0;
     color: var(--midgrey);
     line-height: 1.5em;
 }

 .gh-langswitch .gh-drop-trigger {
     display: inline-block;
     padding: 4px 8px 4px 0;
     color: color-mod(var(--blue) l(-5%));
 }

 .gh-langswitch .gh-drop-trigger span {
     display: flex;
     align-items: center;
 }

 .gh-langswitch svg {
     margin-left: 4px;
     width: 7px;
     height: auto;
 }

 .gh-langswitch .gh-dropdown {
     right: -65px;
     width: 130px;
 }

 .gh-langswitch .gh-dropdown .gh-badge-outline {
     transition: all 0.4s ease;
 }

 .gh-langswitch a:hover .gh-badge-outline {
     border-color: color-mod(var(--darkgrey) l(+15%));
     color: color-mod(var(--darkgrey) l(+15%));
 }

 .gh-langswitch .active a {
     color: var(--blue);
 }

 .gh-langswitch .active .gh-badge-outline,
 .gh-langswitch .active a:hover .gh-badge-outline {
     border-color: var(--blue);
     color: var(--blue);
 }

 .gh-foot-social {
     display: flex;
     align-items: center;
 }

 .gh-foot-social a {
     display: block;
     padding: 7px;
 }

 .gh-foot-social a:first-of-type {
     padding-left: 0;
 }

 .gh-foot-social svg {
     width: auto;
     height: 14px;
     fill: color-mod(var(--midgrey) l(+10%));
     transition: all 0.3s ease;
 }

 .gh-foot-social a:hover svg {
     fill: var(--darkgrey);
 }

 .social-link-mobilemenu svg {
     height: 3rem !important;
     margin: 1rem 1rem;
 }
.social-link-mobilemenu a:last-of-type {
     float: right;
     padding-right: 2rem;
 }
 .social-link-fb svg {
     height: 1.5rem;
     fill: rgb(37, 14, 167)
 }
  .social-link-tw svg {
      height: 1.5rem;
      fill: rgb(54, 195, 238)
  }
  .social-link-in svg {
      height: 1.5rem;
      fill: rgb(211, 57, 129)
  }
  .social-link-yo svg {
       height: 1.5rem;
       fill: rgb(197, 43, 43)
  }
 .social-link-wb img{
     height: 1.9rem;
 }

 .social-link-rss svg {
     height: 1.9rem;
 }

 .social-link-wa svg {
     width: auto;
     height: 2.3rem;
     border-radius: 50%;
     -webkit-border-radius: 50%;
     -moz-border-radius: 50%;
     box-shadow: 0 6px 8px 2px rgba(0, 0, 0, .14);
     -webkit-box-shadow: 0 6px 8px 2px rgba(0, 0, 0, .14);
     -moz-box-shadow: 0 6px 8px 2px rgba(0, 0, 0, .14);

     fill: rgb(4, 136, 22) !important;
 }

 .gh-foot-copyright {
     position: absolute;
     bottom: 3px;
     left: 0;
     margin-top: 5px;
     color: var(--midgrey);
     font-size: 1.1rem;
 }
.gh-floating-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -102.5px;
    width: 85px;
    height: 85px;
    background: #fff;
    border-radius: 50%;
    box-shadow: 0 0 1px rgba(0, 0, 0, .1), 0 6px 23px 0 rgba(0, 0, 0, .08);
}
 @media (max-width: 965px) {
     .gh-foot-col:last-of-type {
         display: none;
     }
 }

 @media (max-width: 800px) {
     .gh-foot {
         text-align: center;
     }

     .gh-foot-content {
         flex-direction: column;
         align-items: initial !important;
     }

     .gh-foot-nav {
         order: 2;
         padding: 0;
         max-width: none;
     }

     .gh-foot-col {
         padding: 0 10px;
     }

     .gh-foot-col:last-of-type {
         display: block;
     }

     .gh-foot-mast {
         order: 1;
         align-items: center;
         margin: 20px 0;
         padding: 20px;
         max-width: 450px;
         width: auto;
         border-top: var(--whitegrey) 1px solid;
         text-align: center;
     }

     .gh-foot-copyright {
         position: static;
     }
 }

 @media (max-width: 550px) {
     .gh-foot-content {
         padding: 0;
         border: none;
     }

     .gh-foot-mast {
         flex: 0 0 auto;
         margin-top: 0;
         padding: 20px 20px 0;
     }

     .hidden-mobile {
         display: none;
     }
     .gh-foot-col:last-of-type {
        display: none;
    }
 }

 .post-fill-footer {
     max-width: 100%;
 }

 /*Special floating footer with no padding or border, activated by bodyclass*/
 .gh-foot-floating .gh-foot {
     padding-top: 2vw;
 }

 .gh-foot-floating .gh-foot-content,
 .gh-foot-floating .gh-foot-mast {
     border-top: none;
 }

 /*Special mini footer used on public properties like zendesk and statuspage*/
 .gh-foot-min {
     padding: 0 4vw 4vw;
     border-top: var(--whitegrey) 1px solid;

 }

 .gh-foot-min .gh-foot-content {
     display: flex;
     padding: 20px 0;
     border-top: none;
 }

 .gh-foot-min-logo svg {
     width: auto;
     height: 22px;
 }

 .gh-foot-min-nav {
     display: flex;
     align-items: center;
     margin-left: -15px;
 }

 .gh-foot-min-back {
     margin-right: -15px;
 }

 .gh-foot-min-item {
     padding: 0 15px;
 }


 @media (max-width: 620px) {
     .gh-grid-item {
         padding: 0 20px 0 20px;
     }
     .gh-section-head p {
    font-size: 2.1rem;
}
 }

 @media (max-width: 660px) {
     .gh-foot-min .gh-foot-content {
         flex-direction: row;
     }

     .gh-foot-min-item:nth-child(1n+2) {
         display: none;
     }
     .gh-signupbox {
        flex-direction: column;
        text-align: center;
    }
    .gh-signup-actions {
        margin: 15px 0 0;
        max-width: none;
        width: 100%;
    }
 }

 .gh-pricing-extra {
     display: flex;
     justify-content: center;
     margin: 3vw -4vw 5vw;
     text-align: center;
 }

 .gh-pricing-extra-item {
     flex: 1 1 50%;
     padding: 30px 4vw;
     max-width: 800px;
 }
 address{
     text-align: left;
 }













